"use client";

import { createContext, useCallback, useEffect, useState } from "react";
import { ReactNode } from "react";

export type ThemeType = "light" | "dark";

interface ThemeContextType {
  theme: ThemeType;
  setTheme: (payload: ThemeType) => void;
}

export const ThemeContext = createContext<ThemeContextType>({
  theme: "light", // ‚úÖ ÏÑúÎ≤Ñ Î†åÎçîÎßÅ Ïãú Í∏∞Î≥∏Í∞í Ïú†ÏßÄ
  setTheme: () => {},
});

function ThemeProvider({ children }: { children: ReactNode }) {
  const [theme, setThemeState] = useState<ThemeType>("light"); // Ï¥àÍ∏∞Í∞íÏùÑ "light"Î°ú ÏÑ§Ï†ï
  const [isMounted, setIsMounted] = useState(false); // ‚úÖ ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏ÏóêÏÑúÎßå UIÎ•º Î†åÎçîÎßÅ

  useEffect(() => {
    setIsMounted(true); // ‚úÖ ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏ÏóêÏÑú Ïã§ÌñâÎê®

    if (typeof window !== "undefined") {
      const storedTheme = localStorage.getItem("theme") as ThemeType | null;
      if (storedTheme === "light" || storedTheme === "dark") {
        setThemeState(storedTheme);
      }
    }
  }, []);

  const setTheme = useCallback((payload: ThemeType) => {
    setThemeState(payload);

    if (typeof window !== "undefined") {
      localStorage.setItem("theme", payload);
    }
  }, []);

  // ‚úÖ ÏÑúÎ≤Ñ Î†åÎçîÎßÅ Ïãú Îπà div Î∞òÌôò ‚Üí ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏ÏóêÏÑúÎßå UIÎ•º Î†åÎçîÎßÅ
  if (!isMounted) {
    return <div style={{ visibility: "hidden" }}>{children}</div>;
  }

  return (
    <ThemeContext.Provider value={{ theme, setTheme }}>
      {children}
    </ThemeContext.Provider>
  );
}

export { ThemeProvider };
